const REQUEST_URL = "https://www.ming-yi.net/";
/**
 * @description 
 * @param {*} url 
 * @param {*} method 
 * @param {*} data 
 * @param {*} type json
 */
export const http = (url, data, method = 'GET', type) => {
	uni.showLoading({
		title: '...'
	});
	return new Promise((resolve, reject) => {
		let wechat_userInfo = uni.getStorageSync('wechat_userInfo')
		let header = {
			'content-type': 'application/json'
		}
		if (type && type == 'form') {
			header['content-type'] = 'application/x-www-form-urlencoded'
		}
		if(wechat_userInfo){
			header.userId = wechat_userInfo.userId;
			header.userName = wechat_userInfo.nickName;
		}
		let newUrl = `${REQUEST_URL}${url}`
		console.log('', url)
		console.log('', method)
		console.log('', data)
		console.log('', type)
		uni.request({
			url: newUrl,
			data,
			header,
			method,
			success: (res) => {
				resolve(res.data)
				uni.hideLoading();
			}
		});
	})
}

export const upload = (url, file) => {
	return new Promise((resolve, reject) => {
		let newUrl = `${REQUEST_URL}${url}`
		uni.uploadFile({
			url: newUrl, //
			filePath: file,
			name: 'file',
			success: (res) => {
				let result = JSON.parse(res.data)
				resolve(result.data.url)
			},
			fail(error) {
				reject(error)
			}
		});
	})


}
